@media screen and (max-width: 768px){
    .cardContainer{
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        width: 100%;
        box-shadow: 1px 1px 5px rgba(0,0,0, .2);
        margin: 10px;
    }
}
.cardContainer{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 30%;
    min-width: 250px;
    min-height: 450px;
    box-shadow: 1px 1px 5px rgba(0,0,0, .2);
    margin: 10px;
}
.cardContainer a{
    align-self: flex-start;
    font-weight: bold;
    font-size: 16px;
    cursor: pointer;
    text-decoration: none;
    color: black;
}
.cardContainer a:hover{
    text-decoration: none;
    color: #ff9100;
}

.formProductPhoto{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 90%;
    height: 70%;
}
.productPhoto{
    margin: 10px;
    width: 90%;
}
