.info{
    display: flex;
    align-self: flex-start;
    text-align: left;
    margin: 5px 10px;
    font-size: 12px;
}
.price{
    font-weight: bold;
    align-self: flex-start;
    margin: 20px 10px;
}

.buttonBuy{
    margin: 0 20px 20px 10px;
    align-self: flex-end;
}
.aboutProductCount{
    align-self: flex-start;
    margin-right: 10px;
    font-size: 12px;
}